import { useRef, useEffect } from "react";
import { register } from "swiper/element/bundle";

register();

export const FaqCarrousel = () => {
  const swiperElRef = useRef(null);

  useEffect(() => {
    const swiperEl = swiperElRef.current;

    swiperEl.breakpoints = {
      0: { slidesPerView: 1, spaceBetween: 10 },
      768: { slidesPerView: 3, spaceBetween: 30 }
    };

    swiperEl.addEventListener("swiperprogress", (e) => {
      const [swiper, progress] = e.detail;
      console.log(progress);
    });

    swiperEl.addEventListener("swiperslidechange", () => {
      console.log("slide changed");
    });
  }, []);

  return (
    <swiper-container
      ref={swiperElRef}
      centered-slides="true"
      initial-slide="1"
      autoplay="true"
      grab-cursor="true"
      autoplay-delay="5000"
      direction="horizontal"
      loop="true"
    >
      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Este programa es solo para Diabetes tipo 1?</p><br />
          <p className="slide-text2">No. Est diseado para personas con Diabetes tipo 1, tipo 2 y Prediabetes, con anlisis de cada paciente y planes personalizados segn cada caso.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Necesito tener experiencia previa en ejercicio fsico y/o alimentacin saludable?</p><br />
          <p className="slide-text2">No. El programa est pensado para acompaarte desde cero, con el apoyo de un equipo profesional que adapta cada plan y mdulos a tu nivel.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Incluye seguimiento personalizado?</p><br />
          <p className="slide-text2">S. Vas a contar con el acompaamiento de un Nutricionista, un Coach Fitness y de Lucas durante todo el proceso, adems de obtener herramientas prcticas, material pregrabado y ms.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Cmo empiezo?</p><br />
          <p className="slide-text2">Pods agendar una llamada directamente en el calendario que encontrars en la pgina. En esa llamada estaremos evaluando tu situacin actual junto al equipo de profesionales, definiendo si podemos ayudarte y as, comenzar a trabajar juntos.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Cunto dura el programa?</p><br />
          <p className="slide-text2">Tiene una duracin completa de 6 meses, con seguimiento personalizado profesional, material terico-prctico pregrabado y desafos prcticos a desarrollar.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Qu resultados puedo esperar?</p><br />
          <p className="slide-text2">Cada proceso es nico, pero los objetivos principales son: Reducir tu HbA1c, lograr rangos estables de glucemia, disminuir (o incluso revertir) el uso de medicacin dependiendo el caso, mejorar tu calidad de vida, aprender herramientas sobre la diabetes y sobre todo, sentirte mejor.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Puedo acceder desde cualquier lugar del mundo?</p><br />
          <p className="slide-text2">S, el programa es posible realizarlo completamente online. Todo lo que necesits es una conexin a internet y las ganas de controlar tu diabetes.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Necesito tener un sensor de glucosa para hacer el programa?</p><br />
          <p className="slide-text2">No es obligatorio, pero si uss uno, ser mucho ms sencillo. Tambin pods hacerlo perfectamente con glucmetro digital.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Este programa reemplaza a mi mdico?</p><br />
          <p className="slide-text2">No. Este programa es complementario al seguimiento mdico profesional que realices. No diagnosticamos, ni tampoco medicamos.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Qu pasa si me cuesta sostener los cambios?</p><br />
          <p className="slide-text2">No ests sola. Por eso trabajamos no solo la alimentacin y el ejercicio como pilares fundamentales, sino tambin la mentalidad, la calidad de sueo y la gestin del estrs, para que los cambios sean sostenibles.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Este programa es solo para jvenes o tambin para adultos mayores?</p><br />
          <p className="slide-text2">Es para cualquier persona mayor de edad. Trabajamos con personas jvenes, adultos y mayores, siempre con un enfoque personalizado.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Este programa es solo para personas con diagnstico reciente?</p><br />
          <p className="slide-text2">No. Funciona tanto si te acaban de diagnosticar como si hace aos vivs con diabetes. Nunca es tarde para mejorar tu calidad de vida.</p>
        </div>
      </swiper-slide>

      <swiper-slide>
        <div className="slide-text-container">
          <p className="slide-text1">Funciona aunque haya probado muchas cosas antes?</p><br />
          <p className="slide-text2">S. Este programa se basa en un enfoque integral basado en los pilares fundamentales de alimentacin, entrenamiento, calidad de sueo y gestin del estrs. Lo que lo hace distinto es el trabajo con acompaamiento 1 a 1, haciendo hincapi en profundidad sobre tus hbitos y tu mentalidad.</p>
        </div>
      </swiper-slide>
    </swiper-container>
  );
};

export default FaqCarrousel;